import React from "react";
import { Component } from "react";
class Footer extends Component {
  render() {
    return (
      <footer
        className="page-footer font-special-color-dark"
        style={{
          background: "rgba(38, 50, 56, 1)",
          width: "100%",
          marginTop: "17rem",
        }}>
        <div className="footer-copyright text-center py-3">
           2020 Copyright:
          <a href="https://foodfactor.herokuapp.com/">
            foodfactor.herokuapp.com
          </a>
        </div>
      </footer>
    );
  }
}

export default Footer;
